import React from 'react';
import {connect} from 'react-redux';
import Users from './Users';
import {getUsersThunk, postUserThunk, putUserThunk, deleteUserThunk, getUserByIdThunk} from '../../redux/users-reducer';
import authRedirect from '../hoc/authRedirect';
import { compose } from 'redux';
import { withRouter } from 'react-router-dom';


class UsersContainer extends React.Component{
    componentDidMount() {
        this.props.getUsersThunk();
    }

    addUser =(newUserData)=> {
        this.props.postUserThunk(newUserData);
    }

    updateUser(newUserData, userId) {
        this.props.putUserThunk(newUserData, userId);
    }

    deleteUser(userId) {
        this.props.deleteUserThunk(userId);
    }

    getUserById(userId) {
        this.props.getUserByIdThunk(userId);
    }
    
    render() {
        debugger;
        return <Users users = {this.props.users} takenUser = {this.props.takenUser} addUser = {this.addUser.bind(this)}
            updateUser = {this.updateUser.bind(this)} deleteUser = {this.deleteUser.bind(this)}
            getUserById = {this.getUserById.bind(this)}/>
    }
}
let mapStateToProps = (state) => {
    return {
        users: state.usersPage.users,
        takenUser: state.usersPage.takenUser
    }    
}
export default compose(
    connect (mapStateToProps, {getUsersThunk, postUserThunk, putUserThunk, deleteUserThunk, getUserByIdThunk}),
    withRouter,
    authRedirect
)(UsersContainer);