import { usersAPI } from '../api/api';

const SHOW_USERS = "SHOW_USERS";
const GET_USER = "GET_USER";

let initialState = {
    users: [],
    takenUser: {
        id: null,
        username: null,
        first_name: null,
        last_name: null,
        is_active: false,
        last_login: null,
        is_superuser: false        
    }
}

export const usersReducer = (state = initialState, action) =>{
    switch (action.type) {
        case SHOW_USERS: {
          return {...state, users: action.users}
        }
        case GET_USER: {
            return {...state, takenUser: action.data}
        }
        default: return state;
    }
}

export const showUsers = (users) => ({type: SHOW_USERS, users});
export const getUser = (data) => ({type: GET_USER, data});

export const getUsersThunk = () =>{ 
    return (dispatch) => {
        debugger;
        usersAPI.getUsers()
        .then(data => {
            dispatch(showUsers(data));            
        });
    }
}

export const postUserThunk = (newUserData) => {
    return (dispatch) => {
        usersAPI.postUser(newUserData);
        usersAPI.getUsers()
        .then(data => {
            dispatch(showUsers(data));            
        });    
    }
}

export const putUserThunk = (newUserData, userId) => {
    return (dispatch) => {
        usersAPI.putUser(newUserData, userId);
        usersAPI.getUsers()
        .then(data => {
            dispatch(showUsers(data));            
        }); 
    }
}

export const deleteUserThunk = (userId) => {
    return (dispatch) => {
        usersAPI.deleteUser(userId);
        usersAPI.getUsers()
        .then(data => {
            dispatch(showUsers(data));            
        }); 
    }
}

export const getUserByIdThunk = (userId) => {
    return (dispatch) => {
        usersAPI.getUserById(userId)
        .then(data => {  //ПОМЕНЯТЬ ЭТОТ ПРОМИС 
            dispatch(getUser(data));            
        });
    }
}