export let sortUsersById = (flag) => {
    let users = document.querySelector("#users");
    if(flag){ // по возврастанию 
        for (let i = 0; i < users.children.length; i++){
            for (let j = i; j < users.children.length; j++){
                if (+users.children[i].getAttribute("id") > +users.children[j].getAttribute("id")){
                    let replacedNode = users.replaceChild(users.children[j], users.children[i]);
                    insertA(replacedNode, users.children[i])
                }                
            }
        }
    }
    else { // по убыванию
        for (let i = 0; i < users.children.length; i++){
            for (let j = i; j < users.children.length; j++){
                if (+users.children[i].getAttribute("id") < +users.children[j].getAttribute("id")){
                    let replacedNode = users.replaceChild(users.children[j], users.children[i]);
                    insertA(replacedNode, users.children[i])
                }                
            }
        }
    }
    return !flag;
}   

export let filterLengthUsername = (lengthVal) => {
    let users = document.querySelector("#users");
    let userArr = [];
    for (let i = 0; i < users.children.length; i++) {
        userArr = users.children[i].attributes.username.value;
        userArr.split();        
        if (+lengthVal === users.children[i].getAttribute("username").length) {
            let replacedNode = users.replaceChild(users.children[i], users.children[0]);
            insertA(replacedNode, users.children[0]);
        }       
    }     
}    
  

let insertA= (elem, refElem) => {
    return refElem.parentNode.insertBefore(elem,refElem.nextSibling)
}