import React from 'react';
import style from "./Login.module.css";
import { reduxForm, Field } from 'redux-form';
import {required, maxLengthCreator} from '../../utils/validators/validators'
import {InputArea} from '../common/FormsControl/FormsControl';
import {  Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { authUserThunk } from '../../redux/auth-reducer';

const length =  maxLengthCreator(20);
let authorization = {
    username: React.createRef(),
    password: React.createRef(),
    rememberMe: React.createRef()
}
let authData = {};

let putData = (authorization) => {
    if (authorization.username.current != null) {
        return (
            authData = {
            username: authorization.username.current.value,
            password: authorization.password.current.value
        }) 
    }
}

const LoginForm = (props) => {
    return (        
        <form  className = {style.items}  onSubmit = {props.handleSubmit} >
            <h4>Enter username and password</h4>
            <div className = {style.border}>   
                <div>
                    <Field className = {style.element} ref = {authorization.username} placeholder = "Username" component = {InputArea} name = "username" validate = {[required, length]}/>
                </div>
                <div>
                    <Field className = {style.element} ref = {authorization.password} placeholder = "Password" component = {InputArea} name = "password" validate = {[required, length]}/>
                </div>
                <div>
                    <button className = {style.element} onClick = {()=>{putData(authorization); props.authorize(authData) }}>Login </button>
                </div>
            </div>
            
        </form>        
    )
}

const LoginReduxForm = reduxForm( {form: 'login'} )(LoginForm)

const Login = (props) => {
    const onSubmit = (fromData) =>{
        debugger;
        props.authUserThunk(fromData);
    } 
    if (props.isAuth) return (
        <Redirect to  = "/users"/>
        ) // className = {style.loginForm}

    return(
        <div>
            <LoginReduxForm onSubmit = {onSubmit} {...props}/>
        </div>
    )    
}
let mapStateToPropsForLogin = (state) =>({
    isAuth: state.authPage.isAuth
})
export default connect(mapStateToPropsForLogin, {authUserThunk})(Login);