import React from 'react';
import styles from "./Users.module.css"
import userLogo from '../../assets/user.png';
import {required, maxLengthCreator} from '../../utils/validators/validators';
import {InputArea} from '../common/FormsControl/FormsControl';
import { reduxForm, Field } from 'redux-form';
import {sortUsersById, filterLengthUsername} from './Logic';

const length =  maxLengthCreator(20);

const UsersForm = (props) => { 
    let id = React.createRef();
    let userId;

    let newData = {
        username: React.createRef(),
        first_name: React.createRef(),
        last_name: React.createRef(),
        password: React.createRef()
    };
    let newUserData = {};
    let checkFlag = true;
    let putData = (newData) => {
        if (newData.username.current != null) {
            return (
                newUserData = {
                username: newData.username.current.value,
                first_name: newData.first_name.current.value,
                last_name: newData.last_name.current.value,
                password: newData.password.current.value,
                is_active: true
            }) 
        }
    }
    let putId = (id) => {
        if (id) {
            return userId = id.current.value;            
        }
    }
debugger;
    return (
        <div className = {styles.page}> 
            <div className = {styles.userList} id = "users">
                {props.users.map(u =>      
                    <div key = {u.id} id = {u.id} username = {u.username} first_name = {u.first_name} last_name = {u.last_name} className = {styles.users}>
                        <img className ={styles.userImage} src = {userLogo} alt =""/>
                        <div className = {styles.userInfo} >
                            <div>ID: {u.id}</div>
                            <div>Username: {u.username}</div>
                            <div>First name: {u.first_name === "" ? "No first name" : u.first_name}</div>
                            <div>Last name: {u.last_name === "" ? "No last name" : u.last_name}</div>
                            <div>Status: {u.is_active ? "active" : "not active"}</div> 
                        </div>
                    </div>)                       
                }                
            </div>
            
            <div className = {styles.userAct}>
                <form onSubmit = {props.handleSubmit} className = {styles.textareas}>
                    <div>Filter by length of username    </div>
                    <input id = "filterLength" onChange = {()=>{filterLengthUsername(document.getElementById("filterLength").value)}} placeholder = "Length of username" /> 

                    <div>Enter ID    </div> <Field ref = {id} placeholder = "ID" component = {InputArea} name = "id" validate = {[required, length]}/>
                    <div>Enter first name    </div> <Field ref = {newData.first_name} placeholder = "First name" component = {InputArea} name = "first_name" validate = {[required, length]}/>
                    <div>Enter last name    </div> <Field ref = {newData.last_name} placeholder = "Last name" component = {InputArea} name = "last_name" validate = {[required, length]}/>
                    <div>Enter username    </div> <Field ref = {newData.username} placeholder = "Username" component = {InputArea} name = "username" validate = {[required, length]}/>
                    <div>Enter password    </div> <Field ref = {newData.password} placeholder = "Password" component = {InputArea} name = "password" validate = {[required, length]}/>
                    
                </form><br />    

                <div className = {styles.buttons}>
                    <button onClick = {() => {putData(newData); props.addUser(newUserData)}} disabled = {true}>Add new user</button><br />
                    <button onClick = {() => {putData(newData); putId(id); props.updateUser(newUserData, userId)}} disabled = {true}>Update user by ID</button><br />
                    <button onClick = {() => {putId(id); props.deleteUser(userId); debugger;}}>Delete user by ID</button> <br />
                    <button onClick = {() => {putId(id); props.getUserById(userId)}}>Get user by ID</button> <br />
                    <button onClick = {() => { checkFlag = sortUsersById(checkFlag);}} >Sort by ID</button>  <br />
                </div>
            </div>

            <div className = {styles.getUser}> Field for getten by ID user
                {props.takenUser.id? 
                <div>
                    <img className ={styles.userImage} src = {userLogo} alt =""/> 
                    <div className = {styles.userInfo} >
                        <div>ID: {props.takenUser.id}</div>
                        <div>Username: {props.takenUser.username}</div>
                        <div>First name: {props.takenUser.first_name === "" ? "No first name" : props.takenUser.first_name}</div>
                        <div>Last name: {props.takenUser.last_name === "" ? "No last name" : props.takenUser.last_name}</div>
                        <div>Status: {props.takenUser.is_active ? "active" : "not active"}</div> 
                    </div>
                </div>                        
                : <div></div>                     
                }
            </div>
        </div>
    )
}

const UsersReduxForm = reduxForm( {form: 'users'} )(UsersForm);
const Users = (props) => {
    const onSubmit = (fromData) =>{
        console.log(fromData);
    } 
    return(
        <div>
            <UsersReduxForm onSubmit = {onSubmit} {...props}/>
        </div>
    )    
}

export default Users;