import React from 'react';
import style from './AboutSite.module.css';
import { reduxForm} from 'redux-form';

const AboutSiteForm = (props) => {
    return (
        <form className = {style.page}>
            <div className = {style.task}>
                <h3>This site was developed for company Empasoft as a test task by Yuriy Biryulin.</h3>
                <span>I was asked to make adaptive SPA site with validative authorization and one of two offered tasks:</span>
                <li>Veiw user list with sorting by ID and filtration by username</li>
                <li>Creating\editing users with validation</li>
            </div>
            <div className = {style.work}>
                <h3>Progress: </h3>
                <span>
                I chose first point and during working ran into a error 500, that was send to me from server on my post and put requests.
                During solving the problem I found out that such error is very rare and it can be connected with Chrome, antivirus and with that the
                server doesn't send the terminal 0-length chunk. I tried to fix it by different ways, but the problem is with chunk and for now I am
                going to fix it. That's why buttons "Add user" and "Update user by ID" are disabled.
                </span>
                <span>
                    So i decided to make sorting and filtrarion because solving error 500 needs a lot of time for me. During making sorting and filtration I didn't
                have difficulties. I had no any criteria that's why filtration was made by length of username. Users with setted length wil be shown first.
                </span>
            </div>
            <div className = {style.report}>
                <h3>Summary of the project: </h3>
                <span>
                    The hole project was made with React and Redux libraries. Project has containers component for each presentation component, that gives attributes
                and functions to presentation component through props. Container component has thunks that help to make axios requests on server from API (application 
                programming interface) and change BLL (buisness logic layer) - redux. Redux is being created with help of function combine that takes such parameters 
                as reducers (reducers are created withfunction combineReducers) and function applyMiddleWare that creates middle layer between our BLL and API. About
                validation I can say that here was added FormsControl and validators to check if nessary input areas are filled.  
                </span>
            </div>
        </form>
    )
}
const AboutSiteReduxForm = reduxForm( {form: 'aboutSite'} )(AboutSiteForm);
const AboutSite = (props) => {
    const onSubmit = (fromData) =>{
        console.log(fromData);
    } 
    return(
        <div>
            <AboutSiteReduxForm onSubmit = {onSubmit} {...props}/>
        </div>
    )    
}
export default AboutSite;