import React from 'react';
import {connect} from 'react-redux';
import Login from './Login';
import {authUserThunk} from '../../redux/auth-reducer';
import { compose } from 'redux';
import { withRouter } from 'react-router-dom';

class LoginContainer extends React.Component {

    authorize (authData) {
        this.props.authUserThunk(authData)
    }

    render() {
        return <Login authToken = {this.props.authToken} authorize = {this.authorize.bind(this)}/>
    }
}
 
let mapStateToProps = (state) => {
    return {
        authToken: state.authPage.authToken
    }  
}

export default compose(connect(mapStateToProps, {authUserThunk}),
    withRouter)
    (LoginContainer);