import * as axios from 'axios';

const instance = axios.create({
    baseURL: `https://emphasoft-test-assignment.herokuapp.com/`,    
    headers:{
        "Authorization" : "Token 781bd9f1de084f4daa7ba2aa8a71a2eab855354e"
    }  
});

//получение инфы авторизированного пользователя 
export const authUserAPI = { 
    login(authData) {
        return axios.post(`https://emphasoft-test-assignment.herokuapp.com/api-token-auth/`,authData)
                    .then( responce => {  return responce.data;});       
    },
    logout(authData){
        return axios.post(`https://emphasoft-test-assignment.herokuapp.com/api-token-auth/`, authData)
    }
}
//получить пользователей
export const usersAPI = {   
    getUsers() {        
        return instance.get(`api/v1/users`)
        .then( responce => {return responce.data;});
    },
    postUser(newUserData) {// попробовать сделать php запрос как в стэковерфлоу
        debugger;
        instance.post(`api/v1/users`,newUserData).then(responce => {responce.flush(); responce.ob_flush(); responce.sleep(2); responce.close()}).catch(error => console.log(error));
        this.getUsers();           
    },
    putUser(newUserData, userId) {
        debugger;
        instance.put(`api/v1/users/` + userId, {data: newUserData}/*, {}*/);
        this.getUsers();
    },
    deleteUser(userId) {
        instance.delete(`api/v1/users/`+userId);
        this.getUsers(); 
    },
    getUserById(userId) {
        return instance.get(`api/v1/users/` + userId)
        .then( responce => {return responce.data;});
    }
}

