import React from 'react';
import style from './Header.module.css';
import {NavLink} from 'react-router-dom';
import Logo from '../../assets/myLogo.png';

const Header = (props) =>{//{props.isAuth ? "Logout" : "Login"} 
/*<div className = {style.item}>                
                    <NavLink to = "/users" activeClassName = {style.active}>Users</NavLink>
                    <NavLink to = "/aboutSite" activeClassName = {style.active}>About site</NavLink>
                    <NavLink to = "/login" activeClassName = {style.active}>Login</NavLink>
                </div>*/
    return(
        <header className = {style.header}>
            <div className = {style.container}>
                <div className = {style.headerBody}>
                    <div className = {style.headerLogo}>
                        <img src = {Logo} alt =""></img>
                    </div>                  
                    <nav className = {style.headerMenu}>
                        <ul className = {style.headerList}>
                            <li>
                                <NavLink to = "/login" className = {style.headerLink} activeClassName = {style.active}>Login</NavLink>
                            </li>
                            <li >
                                <NavLink to = "/users" className = {style.headerLink} activeClassName = {style.active}>Users</NavLink>
                            </li>
                            <li>
                                <NavLink to = "/aboutSite" className = {style.headerLink} activeClassName = {style.active}>About site</NavLink>
                            </li>
                            
                        </ul>
                    </nav>
                </div>
            </div>
            <script type="text/javascript" src="https://code.jquery.com/jquery-3.4.1.slim.min.js"></script>
            <script type="text/javascript" src="./burgerMenuScript.js"></script>
  
        </header>

    );
}


export default Header;