import {authUserAPI} from '../api/api';

const SET_USERS_DATA = "SET_USERS_DATA";

export let initialState = {
    authToken: "",
    isAuth: false 
}

export const authReducer = (state = initialState, action) =>{
    switch (action.type) {
        case SET_USERS_DATA: {
          return {...state, authToken: action.Token, isAuth: true}
        }
        default: return state;
    }
}


export const authUsers = (Token) => ({type: SET_USERS_DATA, Token});

export const authUserThunk = (authData) => (dispatch) =>  {
    authUserAPI.login(authData).then(responce =>{
        dispatch(authUsers(responce.token));
        return responce.token;
    })    
}
